@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Asap:wght@400;700&family=Raleway:wght@700&display=swap");
.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

* {
  padding: 0;
  margin: 0;
  font-family: "Asap", sans-serif;
}

main {
  width: 100%;
  margin: 0 auto;
  height: 100vh;
}

.header_container {
  width: 100%;
  position: relative;
  z-index: 100;
  display: flex;
  justify-content: center;
}
.header_container .logo_left {
  margin-top: 10px;
}
.header_container .nav-bar {
  position: absolute;
  top: 0;
  right: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: position 2.9s;
  -webkit-transition: position 2.9s;
  -moz-transition: position 2.9s;
  /*  -ms-transition: position 2.9s;*/
  -o-transition: position 2.9s;
}
.header_container .nav-bar .toggle-menu {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 40px;
  height: 28px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 2px 2px 2px 3px;
  cursor: pointer;
}
.header_container .nav-bar .toggle-menu .line {
  width: 100%;
  height: 3px;
  border-radius: 5px;
  background-color: #000;
  transition: transform 0.2s ease-out;
  -webkit-transition: transform 0.2s ease-out;
  -moz-transition: transform 0.2s ease-out;
  /* -ms-transition: transform 0.2s ease-out;*/
  -o-transition: transform 0.2s ease-out;
}
.header_container .nav-bar .nav-list {
  list-style: none;
  list-style: none;
  width: 100%;
  line-height: 60px;
  text-align: center;
  background: white;
}
.header_container .nav-bar .nav-list .nav-list-item {
  text-align: center;
  font-family: "Asap", sans-serif;
  background-color: #EAEAEA;
  margin: 20px;
  border-radius: 15px;
}
.header_container .nav-bar .nav-list .nav-list-item .nav-link {
  color: #000000;
  font-size: 30px;
  text-decoration: none;
  position: relative;
  padding-bottom: 4px;
}
.header_container .nav-bar .nav-list .nav-list-item .nav-link .nav-link::before {
  position: absolute;
  content: "";
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #000000;
  transform: scaleX(0);
  transition: transform 0.4s ease-in-out;
  transform-origin: left;
  -webkit-transition: transform 0.4s ease-in-out;
  -moz-transition: transform 0.4s ease-in-out;
  /* -ms-transition: transform 0.4s ease-in-out;*/
  -o-transition: transform 0.4s ease-in-out;
}
.header_container .nav-bar .nav-list .nav-list-item .nav-link .nav-list-item:hover {
  color: #00ff21;
}
.header_container .nav-bar .nav-list .nav-list-item .nav-link .nav-link:hover::before {
  transform: scaleX(1);
}
.header_container .nav-bar .nav-list .logud {
  background-color: #DC2F34;
}
.header_container .nav-bar .nav-list .logud .nav-link {
  color: #FFFFFF;
}
.header_container .nav-bar .logo {
  margin: 20px 10px;
}
.header_container .toggle {
  left: 0;
  height: inherit;
}

.toggle .line1 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(-45deg) translate(-6px, 4px);
}

.toggle .line2 {
  display: none;
}

.toggle .line3 {
  background-color: #000000;
  transform: scale(0.9) rotateZ(45deg) translate(-6px, -4px);
}

/*Master footer*/
footer {
  visibility: visible;
  background-color: #00904A;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 5rem;
}
footer .footer_item {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 50px;
  text-align: center;
}
footer .footer_item .footer_wrap {
  text-decoration: none;
  color: #FFFFFF;
  margin-left: 5px;
}